import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';

import { UsersService } from './users.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss'],
})
export class UsersComponent implements OnInit {
  items = [];
  modalRef: any;
  constructor(
    private userService: UsersService,
    private modalService: BsModalService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {
    this.items = [];
    this.userService.getAll();
    this.userService.users.subscribe((data) => {
      if (data) {
        this.items = data;
      }
    });
  }

  reloadItems($event) {}
  FilterRecords(FilterRecord) {}

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  addLeader() {
    this.modalRef.hide();
    this.userService.addLeader(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.userService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deleteLeader() {
    this.modalRef.hide();
    this.userService.deleteLeader(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.userService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  addOwner() {
    this.modalRef.hide();
    this.userService.addOwner(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.userService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deleteOwner() {
    this.modalRef.hide();
    this.userService.deleteOwner(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.userService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  lockElement() {
    this.modalRef.hide();
    this.userService.lockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.userService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  unlockElement() {
    this.modalRef.hide();
    this.userService.unlockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.userService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
